import React, {Component} from 'react'
import {YMaps, Map, Placemark, ZoomControl} from 'react-yandex-maps'
import PropTypes from 'prop-types'

class MyMap extends Component {
    static propTypes = {
        centerMap: PropTypes.array
    }

    constructor(props) {
        super(props)
        this.state = {}
    }

    render() {
        let centerMap = this.props.centerMap || [55.75, 37.57]
        return (
            <YMaps>
                <Map
                    defaultState={{
                        center: centerMap,
                        zoom: 9,
                        // behaviors: ["disable('scrollZoom')"],
                        zoomControl: true,
                        controls: [],
                        options: {
                            size: "small"
                        }
                    }}
                    style={{
                        position: 'absolute',
                        width: '100%',
                        height: '100%',
                    }}
                >
                    <ZoomControl options={{ float: 'right' , size: "small", position:{right:40, top: 200}}} />
                    <Placemark
                        defaultGeometry={centerMap}
                    />
                </Map>
            </YMaps>
        )
    }
}

export {MyMap}
